.main-navigation__menu-btn {
  width: 2rem;
  height: 2rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: .7rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 2rem;
  height: 1.5px;
  background: #d4dcff;
}


.main-navigation__title {
  color: #d4dcff;
}

.main-navigation__title a {
  text-decoration: none;
  color: #d4dcff;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

.barre-nav {
	  margin: 0rem 2rem;
    color: #1d2761;
    background: #1d2761;
    border: 1px solid #1d2761;
    border-radius: 8px;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

.main-navigation__header-nav {
	display: flex;
}
.main-navigation__icon{
	color: #d4dcff;
	width: 3rem;
	height: 3rem;
}
}
@media screen and (max-width: 30em) {
	.main-navigation__title .logo{
		padding: 0;
	}
}