@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border-radius: 999px;
  color: #f8f8fa;
  border: none;
  cursor: pointer;
  margin-top: 1rem;
  text-decoration: none;
  display: inline-block;
  background: linear-gradient(110deg, #FF0038 50%, #FF0038 50%);

}
.button-reverse {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border-radius: 999px;
  color: #f8f8fa;
  border: none;
  cursor: pointer;
  margin-top: 1rem;
  text-decoration: none;
  display: inline-block;
	background: linear-gradient(110deg, #262947 50%, #262947 50%);
}

.button:focus {
  outline: none;
}

.button--inverse {
  background: transparent;
  border: none;
  padding: 0.7rem;
  border-radius: 999px;
  color: #f8f8fa;
  /*background: linear-gradient(to right, #7589ff 0%, #1c204c 100%);*/
	background: linear-gradient(110deg, #1BB453 50%, #2ec767 50%);
	  background: linear-gradient(110deg, #262947 50%, #262947 50%);
  font-size: 0.9rem;
  cursor: pointer;
}

.button--danger {
  background: var(--red);
  border-color: var(--red);
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background: #1c204c;
	box-shadow: 0px 2px 8px #1c204c;
  color: #d4dcff;
}

.modal__header {
  width: 100%;
  padding: 0.5rem 0.5rem;
  background: #1c204c;
  color: #d4dcff !important;
}
.modal__title {
  color: #d4dcff;
}
.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  display: flex;
  justify-content: flex-end;
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}


@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

