* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --color-dark: #f0f3ff;
  --white: #f8f8fa;
  --orange: #fb743e;
  --orange-gold: #b39836;
  --orange-light: #f38f28;
  --color: #d4dcff;
  --color-light: #1c204c;
  --color-inversed: #d4dcff;
  --contrast: #5396e7;
  --gap: 20px;
  --space: 8px;
  --border: #2a2e5e;
  --border-light: #161e3b;
  --background: #171933;
  --background-light: #1b1e3d;
  --card-bg: #22244d;
  --card-bg-footer: #1d1f42;
  --shadow: rgba(0, 0, 0, 0.2);
  --space-section: 150px;
  --card-footer: #f7fafd;
  --card-padding: 16px;
  --header-height: 137px;
  --play: #1d1d3a;
  --plays: #152167;
  --list-hover: #282a5a;
  --card-footer: #1b1d3d;
  --skeleton: #0f1224;
  --skeleton-wave: #171933;
  --red: #a6333f;
  --selection-background: #3d59a1;
  --container-padding: 20px;
  --container-width: calc(100vw - var(--container-padding) * 2);
}

html {
  font-family: 'Roboto', sans-serif;
  font-family: 'Inter', sans-serif;
}

body {
  margin: 0;
  background: #0e112f;
}
body,
html {
  height: 100%;
}

main {
  margin-top: 0rem;
  height: 90vh;
}

.container-page {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.nav-left {
  width: 15%;
  background: #0f122f;
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
}

.content-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 85%;
}

.main {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  margin-top: 0;
}
.wave,
.wave-inverse {
  position: absolute;
  z-index: -1;
  background-color: #0e112f;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-search {
  display: flex;
  /* flex-direction: row-reverse; */
  align-items: center;
  justify-content: flex-end;
  height: 3rem;
  margin-bottom: 3rem;
}

.search-active {
  display: flex;
  justify-content: flex-end;
  margin: 0;
}

.btn-active-seach {
  background: transparent;
  border: none;
  color: #e2e5e9;
  cursor: pointer;
}
.btn-active-seach .MuiSvgIcon-root {
  font-size: 2rem;
  width: 1em;
  height: 1em;
}

.btn-active-seach:focus,
.btn-active-seach:hover,
.btn-active-seach:active,
.btn-active-seach:focus-visible,
.btn-active-seach:focus-within,
.btn-active-seach:visited {
  background: transparent;
  outline: none;
  border: none;
}

.form-group {
  width: 100%;
	padding: 1rem 0rem;
}

h2 {
  margin-left: 0rem !important;
}

.color-role {
	color: #FF0038;
}

.normalize-label {
  background-color: #15183a !important;
}
@media screen and (max-width: 30em) {
  .nav-left {
		display: none;
  }
  .content-right {
		width: 100%;
  }
}



@media (max-width: 768px) {
.header-search {
    height: 100% !important;
}
